import React, { useState, useEffect } from "react";
import { Line } from "react-chartjs-2";
import axios from "axios";
import './App.css';

let empAge = [20, 12, 123, 435, 12, 531, 34, 12, 64, 75];
let empAgeBB = [2, 1, 12, 43, 1, 53, 4, 1, 6, 7];
let empSal = [98000, 98000, 98000, 98000, 98000, 98000, 98000, 98000, 98000, 98000];
let empSalBB = [104500, 104500, 104500, 104500, 104500, 104500, 104500, 104500, 104500, 104500];

function Dankmemes(props) {
    const [chartData, setChartData] = useState({});
    const [chartDataBB, setChartDataBB] = useState({});
    // const [employeeSalary, setEmployeeSalary] = useState([]);
    // const [employeeAge, setEmployeeAge] = useState([]);
    // const [x, setDolar] = useState([]);
    let dolar = 0;

    const chart = () => {


        axios.get('https://api.nbp.pl/api/exchangerates/rates/a/usd/last/1/?format=json')
            .then(res => {

                dolar = res.data.rates[0].mid;

            })
            .catch(err => {
                // console.log(err)
            })

        console.log("Cena" + dolar);

        axios.get('https://api.bitbay.net/rest/trading/ticker/BTC-PLN')
            .then(res => {
                //console.log(res.data.ticker.rate)
                //setBB(res.data.ticker.rate)
                //xbb = res.data.ticker.rate;

                for (let i = 0; i < 10; i++) {
                    empSalBB[i] = empSalBB[i + 1];
                }
                empSalBB[9] = (res.data.ticker.rate);


            })
            .catch(err => {
                //console.log(err)
            })

        axios
            .get("https://cors-anywhere.herokuapp.com/www.bitstamp.net/api/v2/ticker/btcusd/")
            .then(res => {
                for (let i = 0; i < 10; i++) {
                    empSal[i] = empSal[i + 1];
                }
                empSal[9] = (((res.data.last) * dolar).toFixed(0));
                for (let i = 0; i < 10; i++) {
                    empAge[i] = empAge[i + 1];
                }
                var d = new Date();
                var n = d.getMinutes();

                empAge[9] = n;


                setChartData({
                    labels: empAge,
                    datasets: [
                        {
                            label: "bitstamp",
                            data: empSal,
                            backgroundColor: ["rgba(75, 192, 192, 0.6)"],
                            borderWidth: 4
                        },
                        {
                            label: "bitbay",
                            data: empSalBB,
                            backgroundColor: ["rgba(5, 92, 92, 0.6)"],
                            borderWidth: 4
                        },

                    ]
                });





            })
            .catch(err => {
                console.log(err);
            });



        console.log(empSal, empAge);



    };

    useEffect(() => {
        setInterval(() => {
            chart();
        }, 20000);
    }, []);
    return (
        <div className="Appx">

            <div>
                <Line
                    data={chartData}
                    options={{
                        responsive: true,
                        title: { text: "THICCNESS SCALE", display: true },
                        scales: {
                            yAxes: [
                                {
                                    ticks: {
                                        min: (empSal[9] - 1400),
                                        // max: ,
                                        autoSkip: true,
                                        maxTicksLimit: 100,
                                        beginAtZero: true
                                    },
                                    gridLines: {
                                        display: false
                                    }
                                }
                            ],
                            xAxes: [
                                {
                                    gridLines: {
                                        display: false
                                    }
                                }
                            ]
                        }
                    }}
                />

            </div>
        </div>
    );
};

export default Dankmemes;